/* Cart Page Styles */
.cart-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 2rem 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
}

.cart-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Cart Header */
.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 1.5rem 2rem;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
}

.cart-header-left {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.cart-header-icon {
  background: linear-gradient(135deg, #0891b2 0%, #0e7490 100%);
  padding: 1rem;
  border-radius: 16px;
  color: white;
  box-shadow: 0 8px 25px rgba(8, 145, 178, 0.3);
}

.cart-header h1 {
  color: #1f2937;
  font-size: 2.25rem;
  font-weight: 800;
  margin: 0;
  background: linear-gradient(135deg, #1f2937 0%, #374151 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.cart-subtitle {
  color: #6b7280;
  font-size: 1rem;
  margin: 0.25rem 0 0 0;
  font-weight: 500;
}

.clear-cart-btn {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
  border: none;
  padding: 0.875rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3);
}

.clear-cart-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(239, 68, 68, 0.4);
}

/* Cart Content Layout */
.cart-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
}

/* Cart Items Section */
.cart-items {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
}

.cart-items-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #e5e7eb;
}

.cart-items-header h3 {
  color: #1f2937;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
}

.items-count {
  background: linear-gradient(135deg, #0891b2 0%, #0e7490 100%);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-weight: 600;
  font-size: 0.875rem;
}

/* Individual Cart Item */
.cart-item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 1.5rem;
  padding: 2rem;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.06);
  border: 1px solid rgba(255, 255, 255, 0.8);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.cart-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #0891b2 0%, #0e7490 50%, #0891b2 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.cart-item:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.12);
}

.cart-item:hover::before {
  opacity: 1;
}

.cart-item-image {
  position: relative;
}

.cart-item-image img {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 16px;
  border: 2px solid #e5e7eb;
  transition: all 0.3s ease;
}

.cart-item:hover .cart-item-image img {
  border-color: #0891b2;
  transform: scale(1.05);
}

.item-number {
  position: absolute;
  top: -8px;
  right: -8px;
  background: linear-gradient(135deg, #0891b2 0%, #0e7490 100%);
  color: white;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 0.875rem;
  box-shadow: 0 4px 15px rgba(8, 145, 178, 0.3);
}

/* Item Details */
.cart-item-details {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.item-main-info {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.cart-item-name {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
  line-height: 1.3;
}

.item-tags {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.tag {
  background: linear-gradient(135deg, #dbeafe 0%, #bfdbfe 100%);
  color: #1e40af;
  padding: 0.375rem 0.75rem;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  border: 1px solid rgba(59, 130, 246, 0.2);
}

/* Pricing Section */
.item-pricing {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}

.price-info {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.current-price {
  font-size: 1.5rem;
  font-weight: 800;
  color: #0891b2;
}

.mrp-price {
  font-size: 1rem;
  color: #9ca3af;
  text-decoration: line-through;
  font-weight: 500;
}

/* Quantity Controls */
.cart-item-quantity {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.cart-item-quantity label {
  font-weight: 600;
  color: #6b7280;
  font-size: 0.875rem;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: #f8fafc;
  padding: 0.25rem;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
}

.quantity-btn {
  background: white;
  border: 1px solid #d1d5db;
  width: 36px;
  height: 36px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s ease;
  color: #6b7280;
}

.quantity-btn:hover:not(:disabled) {
  background: #0891b2;
  color: white;
  border-color: #0891b2;
  transform: scale(1.05);
}

.quantity-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.quantity-display {
  min-width: 48px;
  text-align: center;
  font-weight: 700;
  color: #1f2937;
  font-size: 1.1rem;
}

/* Subtotal */
.cart-item-subtotal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, #f0fdf4 0%, #dcfce7 100%);
  padding: 1rem 1.5rem;
  border-radius: 12px;
  border: 1px solid rgba(34, 197, 94, 0.2);
}

.subtotal-label {
  font-weight: 600;
  color: #059669;
  font-size: 0.875rem;
}

.subtotal-amount {
  font-weight: 800;
  color: #059669;
  font-size: 1.25rem;
}

/* Remove Button */
.cart-item-actions {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.remove-item-btn {
  background: linear-gradient(135deg, #fef2f2 0%, #fee2e2 100%);
  color: #dc2626;
  border: 1px solid #fecaca;
  padding: 0.75rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-item-btn:hover {
  background: linear-gradient(135deg, #fee2e2 0%, #fecaca 100%);
  border-color: #fca5a5;
  transform: scale(1.05);
  box-shadow: 0 4px 15px rgba(220, 38, 38, 0.2);
}

/* Cart Summary */
.cart-summary {
  position: sticky;
  top: 2rem;
}

.cart-summary-card {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  margin-bottom: 1.5rem;
}

.summary-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #f3f4f6;
}

.summary-header svg {
  color: #0891b2;
}

.summary-header h3 {
  color: #1f2937;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
}

.summary-details {
  margin-bottom: 2rem;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  color: #6b7280;
  font-size: 1rem;
}

.summary-row.total {
  font-size: 1.5rem;
  font-weight: 800;
  color: #1f2937;
  border-top: 2px solid #f3f4f6;
  padding-top: 1.5rem;
  margin-top: 1rem;
}

.free-shipping {
  color: #059669;
  font-weight: 700;
}

.free-shipping-notice {
  background: linear-gradient(135deg, #fef3c7 0%, #fde68a 100%);
  color: #92400e;
  padding: 1rem;
  border-radius: 12px;
  margin: 1rem 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
  font-weight: 600;
  border: 1px solid rgba(245, 158, 11, 0.2);
}

.free-shipping-notice svg {
  color: #f59e0b;
  flex-shrink: 0;
}

/* Checkout Button */
.checkout-btn {
  width: 100%;
  background: linear-gradient(135deg, #0891b2 0%, #0e7490 100%);
  color: white;
  border: none;
  padding: 1.25rem;
  border-radius: 16px;
  font-size: 1.125rem;
  font-weight: 700;
  cursor: pointer;
  margin: 1.5rem 0 1rem 0;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  box-shadow: 0 8px 25px rgba(8, 145, 178, 0.3);
}

.checkout-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(8, 145, 178, 0.4);
}

/* Continue Shopping Link */
.continue-shopping-link {
  display: block;
  text-align: center;
  color: #0891b2;
  text-decoration: none;
  font-weight: 600;
  padding: 1rem;
  border: 2px solid #0891b2;
  border-radius: 16px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.continue-shopping-link:hover {
  background: linear-gradient(135deg, #0891b2 0%, #0e7490 100%);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(8, 145, 178, 0.3);
}

/* Cart Benefits */
.cart-benefits {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 20px;
  padding: 1.5rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
}

.benefit-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 0;
  color: #6b7280;
  font-size: 0.875rem;
  font-weight: 500;
}

.benefit-item:not(:last-child) {
  border-bottom: 1px solid #f3f4f6;
}

.benefit-item svg {
  color: #0891b2;
  flex-shrink: 0;
}

/* Empty Cart Styles */
.cart-empty {
  min-height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart-empty-content {
  text-align: center;
  max-width: 500px;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  padding: 3rem;
  border-radius: 24px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
}

.cart-empty-icon {
  color: #0891b2;
  margin-bottom: 2rem;
  opacity: 0.8;
}

.cart-empty h2 {
  color: #1f2937;
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #1f2937 0%, #374151 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.cart-empty p {
  color: #6b7280;
  margin-bottom: 2.5rem;
  font-size: 1.125rem;
  line-height: 1.6;
}

.continue-shopping-btn {
  background: linear-gradient(135deg, #0891b2 0%, #0e7490 100%);
  color: white;
  text-decoration: none;
  padding: 1.25rem 2.5rem;
  border-radius: 16px;
  font-weight: 700;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 8px 25px rgba(8, 145, 178, 0.3);
}

.continue-shopping-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(8, 145, 178, 0.4);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .cart-content {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .cart-summary {
    position: static;
  }
}

@media (max-width: 768px) {
  .cart-page {
    padding: 1rem 0;
  }
  
  .cart-header {
    flex-direction: column;
    gap: 1.5rem;
    text-align: center;
    padding: 1.5rem;
  }
  
  .cart-header h1 {
    font-size: 1.75rem;
  }
  
  .cart-content {
    gap: 1rem;
  }
  
  .cart-items, .cart-summary-card, .cart-benefits {
    padding: 1.5rem;
  }
  
  .cart-item {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 1.5rem;
    padding: 1.5rem;
  }
  
  .cart-item-image img {
    width: 100px;
    height: 100px;
  }
  
  .item-pricing {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }
  
  .cart-item-quantity {
    justify-content: center;
  }
  
  .cart-item-subtotal {
    flex-direction: column;
    gap: 0.5rem;
    text-align: center;
  }
  
  .cart-header-left {
    flex-direction: column;
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .cart-container {
    padding: 0 0.5rem;
  }
  
  .cart-header, .cart-items, .cart-summary-card, .cart-benefits {
    padding: 1rem;
  }
  
  .cart-item {
    padding: 1rem;
  }
  
  .cart-header h1 {
    font-size: 1.5rem;
  }
  
  .cart-empty-content {
    padding: 2rem 1.5rem;
  }
  
  .cart-empty h2 {
    font-size: 1.5rem;
  }
}
/* Loading State */
.cart-loading {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 24px;
  padding: 3rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid #e5e7eb;
  border-top: 4px solid #0891b2;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.cart-loading p {
  color: #6b7280;
  font-size: 1.125rem;
  font-weight: 500;
  margin: 0;
}
/* Smoo
th transitions for cart updates */
.cart-item {
  transition: all 0.2s ease-in-out;
}

.cart-item:hover {
  transform: translateY(-2px);
}

.quantity-display {
  transition: all 0.15s ease-in-out;
}

.cart-item-subtotal .subtotal-amount {
  transition: all 0.15s ease-in-out;
}

.summary-row span:last-child {
  transition: all 0.15s ease-in-out;
}

/* Prevent layout shifts during updates */
.cart-items {
  min-height: 200px;
}

.cart-summary-card {
  position: sticky;
  top: 2rem;
}

/* Smooth loading transitions */
.cart-loading {
  transition: opacity 0.3s ease-in-out;
}

/* Prevent flickering during rapid updates */
.cart-item * {
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

/* Skeleton Loading Styles */
.skeleton {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
  border-radius: 8px;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

/* Skeleton Item Styles */
.skeleton-item {
  pointer-events: none;
  opacity: 0.7;
}

.skeleton-image {
  width: 120px;
  height: 120px;
  border-radius: 16px;
}

.skeleton-number {
  position: absolute;
  top: -8px;
  right: -8px;
  width: 28px;
  height: 28px;
  border-radius: 50%;
}

.skeleton-title {
  height: 24px;
  width: 80%;
  margin-bottom: 12px;
}

.skeleton-tag {
  height: 20px;
  width: 80px;
  border-radius: 20px;
  margin-right: 8px;
}

.skeleton-price {
  height: 28px;
  width: 100px;
  margin-right: 12px;
}

.skeleton-mrp {
  height: 20px;
  width: 80px;
}

.skeleton-label {
  height: 16px;
  width: 60px;
  margin-bottom: 8px;
}

.skeleton-btn {
  width: 36px;
  height: 36px;
  border-radius: 8px;
}

.skeleton-quantity {
  width: 48px;
  height: 20px;
  margin: 0 8px;
}

.skeleton-subtotal-label {
  height: 16px;
  width: 70px;
}

.skeleton-subtotal-amount {
  height: 24px;
  width: 90px;
}

.skeleton-remove-btn {
  width: 40px;
  height: 40px;
  border-radius: 12px;
}

/* Header Skeleton Styles */
.skeleton-header-icon {
  width: 48px;
  height: 48px;
  border-radius: 16px;
}

.skeleton-header-title {
  height: 36px;
  width: 200px;
  margin-bottom: 8px;
}

.skeleton-header-subtitle {
  height: 20px;
  width: 150px;
}

.skeleton-clear-btn {
  height: 44px;
  width: 120px;
  border-radius: 12px;
}

/* Items Header Skeleton */
.skeleton-items-title {
  height: 28px;
  width: 120px;
}

.skeleton-items-count {
  height: 24px;
  width: 80px;
  border-radius: 20px;
}

/* Summary Skeleton Styles */
.skeleton-icon {
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

.skeleton-summary-title {
  height: 28px;
  width: 140px;
}

.skeleton-summary-label {
  height: 20px;
  width: 100px;
}

.skeleton-summary-value {
  height: 20px;
  width: 80px;
}

.skeleton-total-label {
  height: 24px;
  width: 60px;
}

.skeleton-total-value {
  height: 24px;
  width: 100px;
}

.skeleton-checkout-btn {
  height: 56px;
  width: 100%;
  border-radius: 16px;
  margin: 24px 0 16px 0;
}

.skeleton-continue-link {
  height: 48px;
  width: 100%;
  border-radius: 16px;
}

/* Benefits Skeleton */
.skeleton-benefit-icon {
  width: 20px;
  height: 20px;
  border-radius: 4px;
  flex-shrink: 0;
}

.skeleton-benefit-text {
  height: 16px;
  width: 200px;
  margin-left: 12px;
}

/* Enhanced shimmer effect for better visual appeal */
@media (prefers-reduced-motion: no-preference) {
  .skeleton {
    background: linear-gradient(
      90deg,
      rgba(240, 240, 240, 0.8) 25%,
      rgba(220, 220, 220, 0.9) 50%,
      rgba(240, 240, 240, 0.8) 75%
    );
    background-size: 200% 100%;
    animation: shimmer 1.8s ease-in-out infinite;
  }
}

/* Respect reduced motion preferences */
@media (prefers-reduced-motion: reduce) {
  .skeleton {
    background: #f0f0f0;
    animation: none;
  }
}

/* Dark mode skeleton support */
@media (prefers-color-scheme: dark) {
  .skeleton {
    background: linear-gradient(90deg, #2a2a2a 25%, #3a3a3a 50%, #2a2a2a 75%);
    background-size: 200% 100%;
  }
}

/* Skeleton loading fade-in effect */
.cart-page {
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}/* S
mooth loading transitions */
.cart-page {
  animation: fadeIn 0.4s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.cart-item {
  transition: all 0.3s ease-in-out;
  animation: slideInUp 0.4s ease-out;
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.cart-item.loading {
  opacity: 0.8;
}

.cart-summary {
  animation: slideInRight 0.4s ease-out;
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Quantity controls loading state */
.quantity-controls {
  position: relative;
}

.quantity-controls.loading {
  pointer-events: none;
}

.quantity-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Smooth transitions for cart operations */
.cart-content {
  transition: opacity 0.3s ease-in-out;
}

.cart-content.updating {
  opacity: 0.7;
}

/* Empty cart animation */
.cart-empty {
  animation: fadeIn 0.5s ease-in-out;
}

.cart-empty-content {
  animation: bounceIn 0.6s ease-out;
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Responsive animations */
@media (max-width: 768px) {
  .cart-page {
    animation-duration: 0.3s;
  }
  
  .cart-item {
    animation-duration: 0.3s;
  }
  
  .cart-summary {
    animation-duration: 0.3s;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .cart-page,
  .cart-item,
  .cart-summary,
  .cart-empty,
  .cart-empty-content {
    animation: none;
  }
  
  .cart-item,
  .cart-content {
    transition: none;
  }
}